:root {
  // Fonts
  --font-general: Montserrat, sans-serif;
  --font-title: Seymour One, sans-serif;

  // Accent color
  --accent-color: #f8d24e;

  //Lines in reviews color
  --lines-reviews-color: #e1e1e1;

  //Text colors
  --primary-lighttext-color: #fcfcfc;
  --secondary-lighttext-color: #ababab;

  --primary-darktext-color: #131313;

  --copyright-text-color: #7a7a7a;

  //Section's background color
  --primary-background-color: #84a178;

  //Product-cards background
  --product-green-background: #aec6a5;
  --product-opacity-green-background: rgba(156, 190, 143, 0.7);

  --product-yellow-background: #efd478;
  --product-opacity-yellow-background: rgba(248, 210, 78, 0.7);

  --product-red-background: #d6936d; //Also use in Contacts section as text color in Company representatives
  --product-opacity-red-background: rgba(213, 117, 63, 0.7);

  //Input field background color
  --input-background: rgba(248, 210, 78, 0.1);

  // Buttons color
  --button-hover-color: #0a8817;
  --button-disable-color: #ababab;

  // Backdrop-color
  --backdrop-color: rgba(0, 0, 0, 0.2);
}
