.footer {
    background-color: var(--primary-background-color);
    @media screen and (min-width:1200px){
        padding-top: 60px;
      }    
}

.container--footer {

    @media screen and (max-width: 767px) {
        margin-bottom: 40px;
    }


    @media screen and (min-width: 768px){
        display: flex;
        justify-content: space-between;
        margin-bottom: 56px;
        
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 60px;
    }
}

.footer-content {
    color: var(--primary-lighttext-color);

    @media screen and (max-width: 767px) {
        max-width: 280px;
        margin-bottom: 42px;
    }

    @media screen and (min-width:768px) and (max-width:1199px){
        max-width: 352px;
        margin-right: 66px;
        margin-bottom: 32px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 54px;
    }

}

.footer-content__text {
        font-weight: 700;
        text-transform: uppercase;
}

.footer-content__text--litle {

    @media screen and (max-width:767px){
        margin-bottom: 16px;
        font-size: 18px;
        line-height: 1.22;
      }
    
    @media screen and (min-width:768px){
        margin-bottom: 9px;
        font-size: 26px;
        line-height: 1.23;
        letter-spacing: 0.04em;  
      }
    
    @media screen and (min-width:1200px){
        margin-bottom: 14px;
        font-weight: 400;
        font-size: 30px;
      }
}

.footer-content__text--big {

    @media screen and (max-width:767px){
        font-size: 34px;
        line-height: 1.21;
        letter-spacing: 0.04em;
      }
    @media screen and (min-width:768px){
        font-size: 48px;
        line-height: 1.23;
        letter-spacing: 0.06em;
    }
    @media screen and (min-width:1200px){
        font-size: 58px;
        line-height: 1.22;
    }
}

.footer-links {
    @media screen and (min-width:768px) {
        display: flex;
        flex-direction: column;
        justify-content: end;
    }
}

.social-network {
    @extend %list;
    display: flex;
    justify-content: flex-end;
    column-gap: 30px;
    
    @media screen and (max-width:767px){
        margin-bottom: 38px;
    }

    @media screen and (min-width:768px){
        margin-bottom: 32px;  
    }


    @media screen and (min-width:1200px){
        margin-bottom: 40px;
    } 
}

.social-network__link {
    border: none;
    border-radius: 50%;
    background: var(--accent-color);
    fill: var(--primary-background-color);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;

    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {    
    fill: var(--primary-lighttext-color);
    }
}

.footer-contacts {
    font-weight: 700;
    line-height: 1.86;

    @media screen and (max-width:767px){
        font-size: 12px;
        text-align: right;
        
    }
    
      @media screen and (min-width:768px){
        font-size: 14px; 
    }

    @media screen and (min-width:1200px){
        font-size: 16px;
        letter-spacing: 0.02em;
    }
}

.footer-contacts__text {
    color: var(--primary-darktext-color);
}

.footer-contacts__tel {
    text-decoration: none;
     color: var(--primary-lighttext-color);
 }

.footer-copyright {
    border-top: 1px solid  var(--lines-reviews-color);
    
    text-align: center;

    font-weight: 700;
    font-size: 14px;

    color: var(--copyright-text-color);

    @media screen and (max-width:767px){
        padding: 16px 0 62px 0;
        line-height: 1.52;
      }
    
      @media screen and (min-width:768px){
        display: flex;
        justify-content: center;
        line-height: 1.86;
        letter-spacing: 0.02em;
        padding: 14px 0 13px 0;  
    }

    @media screen and (min-width:1200px){
        margin-left: auto;
        margin-right: auto;
        padding: 12px 0 11px 0;

        font-size: 12px;
        width: 1170px;
        
    }
}